import React, { useState } from 'react';

import {
    MessageOutlined,
    CompassOutlined,
    HeartOutlined,

    UserOutlined
} from '@ant-design/icons';

import IconHome from './Icons/IconHome.jsx';
import {Avatar,Popover} from 'antd';
import { Redirect,Link } from 'react-router-dom';

const IconsHeader = () =>{
    const [redirect,setRedirect] = useState(false);
    const [path,setPath]=useState(null);
    const content = (
        <div>
            <p style={{cursor:'pointer'}} onClick={()=>{setRedirect(true);setPath('/profile')}}>
                Perfil
            </p>
            <p style={{cursor:'pointer'}} onClick={()=>{
                localStorage.removeItem('token_access');
                if(window.location.pathname==='/')window.location.reload();
                window.location.replace('/');
            }}>Salir</p>
        </div>
    );
    if(redirect)return<Redirect to={path}/>

    return(
        <div style={{display:'flex',justifyContent:'space-between',alignItems:'center',height:'100%'}} >
            <Link to='/' style={{color:'inherit'}}>
                <IconHome />
            </Link>
            
            <MessageOutlined style={{fontSize:25}}/>
            <CompassOutlined style={{fontSize:25}}/>
            <HeartOutlined style={{fontSize:25}}/>
            <Popover content={content}>
                <Avatar size={25} icon={<UserOutlined />} />
            </Popover>
        </div>
    );
};

export default IconsHeader;

