import React from 'react';
import {Col,Row} from 'antd';
import Histories from './Histories.jsx';
import SideInfo from './SideInfo.jsx';
import AddPost from './AddPost.jsx';
import './Main.css';
const Main = () =>{
    return(
        <main id='main_container'>
            <Row>
                <Col xs={24} lg={16}>
                    <Histories/>
                    <AddPost/>
                </Col>
                <Col xs={0} lg={8}>
                    <SideInfo/>
                </Col>
            </Row>
        </main>
    );
};

export default Main;